#example-mod {
  animation: fade 0.5s linear;
  background-color: black;
} 


.close-btn {
  // background-color: #ff00c7;
  color: grey;
  border-radius: 50%;
  position: absolute;
  right: 40px;
  top: 0px;
  outline: none ;
  border: none ;
  border-radius: 8px;
  text-align: center;
  font-size: 60px;
  font-weight: bold;
  height: 30px;
  width: 30px;
}

.close-btn:hover {
  // filter: brightness(85%);
  color: black;
  outline: none !important;
}


@keyframes fade {
  0% { opacity: 0 }
  100% { opacity: 1 }
}